import { createJob } from 'apis/modules/Jobs';
import JobForm from 'components/Form/JobForm';
import Routers from 'constants/routers';
import dayjs from 'dayjs';
import noti from 'libs/notification';
import { IJobForm } from 'models/jobs';

import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';

const CreatePage: React.FC = () => {
  const [isProcess, setIsProcess] = useState(false);
  const { t } = useTranslation();

  const navi = useNavigate();
  const onSubmit = async (values: IJobForm, skills: string[]) => {
    try {
      setIsProcess(true);
      const payload = {
        jobName: values.jobName,
        workingTime: values.workingTime,
        city: values.city,
        experience: values.experience,
        startDate: dayjs(values.startDate).format('YYYY-MM-DD'),
        endDate: dayjs(values.endDate).format('YYYY-MM-DD'),
        budget: values.budget && isNaN(values.budget) ? 0 : Number(values.budget),
        requirement: values.requirement,
        description: values.description,
        department: values.department,
        skills
      };
      const response = (await createJob(payload)) as any;
      if (response && response.code === 'SUCCESS') {
        noti('success', 'Create successfully');
        navi(Routers.Jobs);
      } else {
        noti('error', response.message);
      }
    } catch (error: any) {
      noti('error', error.message);
    } finally {
      setIsProcess(false);
    }
  };

  return (
    <div className="h-full">
      <div className="h-16 bg-white w-full">
        <div className="flex h-full flex-row justify-between items-center px-6">
          <div className="text-xl font-bold">{t('page.jobs.titleCreate')}</div>
        </div>
      </div>
      <div className="mt-4 mb-12 mx-7 rounded-2xl p-4 bg-white">
        <JobForm isProcess={isProcess} onSubmit={onSubmit} />
      </div>
    </div>
  );
};

export default CreatePage;
