import { deleteRequest, getRequest, patchRequest, postRequest, putRequest } from 'apis/services';
import { IJobForm } from 'models/jobs';

function createJob(newItem: IJobForm) {
  return postRequest({
    url: '/admin/jobs',
    payload: newItem
  });
}

function getListJob(params: any) {
  return getRequest({
    url: '/admin/jobs',
    params
  });
}

function updateJob(id?: number | string, updatedItem?: IJobForm | any) {
  return putRequest({
    url: `/admin/jobs/${id}`,
    payload: updatedItem
  });
}

function deleteJob(id: number | string) {
  return deleteRequest({
    url: `/admin/jobs/${id}`
  });
}

function getJobById(id: number | string) {
  return getRequest({
    url: `/admin/jobs/${id}`
  });
}

function updateStatusJob(id: number | string, status: string) {
  return patchRequest({
    url: `/admin/jobs/${id}`,
    payload: {
      status
    }
  });
}

export { createJob, deleteJob, getJobById, getListJob, updateJob, updateStatusJob };
