import { CloseOutlined } from '@ant-design/icons';
import { Button, Col, DatePicker, Form, Input, Row, Select, Tag } from 'antd';
import { getDepartments, getExperiences, getLocations, getWorkingTime } from 'apis/modules/Common';
import dayjs from 'dayjs';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import { IJobForm } from 'models/jobs';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';

dayjs.extend(customParseFormat);

interface IProps {
  textButton?: string;
  onSubmit(job: IJobForm, skills: string[], type?: string): void;
  item?: IJobForm;
  isProcess?: boolean;
}

function JobForm(props: IProps) {
  const { t } = useTranslation();
  const navi = useNavigate();
  const [form] = Form.useForm();
  const dateFormat = 'YYYY-MM-DD';
  const { item, onSubmit, textButton, isProcess } = props;
  const [skillsForm, setSkillsForm] = useState<string[]>([]);
  const [options, setOptions] = useState({
    department: [],
    workingTime: [],
    yearOfExperience: [],
    location: []
  });

  function formatOptions(options: any) {
    return options.map((el: any) => ({ label: el, value: el }));
  }

  async function getYearOfExperienceOptions() {
    try {
      const response = await getExperiences();
      if (response && response.data) {
        const options = formatOptions(response.data);
        setOptions((prev) => ({ ...prev, yearOfExperience: options }));
      }
    } catch (error) {
      setOptions((prev) => ({ ...prev, yearOfExperience: [] }));
    }
  }

  async function getDepartmentOptions() {
    try {
      const response = await getDepartments();
      if (response && response.data) {
        const options = formatOptions(response.data);
        setOptions((prev) => ({ ...prev, department: options }));
      }
    } catch (error) {
      setOptions((prev) => ({ ...prev, department: [] }));
    }
  }

  async function getWorkingTimeOptions() {
    try {
      const response = await getWorkingTime();
      if (response && response.data) {
        const options = formatOptions(response.data);
        setOptions((prev) => ({ ...prev, workingTime: options }));
      }
    } catch (error) {
      setOptions((prev) => ({ ...prev, workingTime: [] }));
    }
  }

  async function getLocationOptions() {
    try {
      const response = await getLocations();
      if (response && response.data) {
        const options = formatOptions(response.data);
        setOptions((prev) => ({ ...prev, location: options }));
      }
    } catch (error) {
      setOptions((prev) => ({ ...prev, location: [] }));
    }
  }

  function onSkillsChange(e: any) {
    if (e.key === 'Enter') {
      e.preventDefault();
      if (!e.target.value || !e.target.value.trim() || skillsForm.includes(e.target.value)) {
        return;
      }

      setSkillsForm((prev) => [...prev, e.target.value.trim()]);
      form.setFieldsValue({
        skills: ''
      });
    }
  }

  function onRemoveSkill(skill: string) {
    const newTags = skillsForm.filter((tag) => tag !== skill);
    setSkillsForm(newTags);
  }

  async function getCommonOptions() {
    await getDepartmentOptions();
    await getLocationOptions();
    await getWorkingTimeOptions();
    await getYearOfExperienceOptions();
  }

  function handleSubmitForm(type?: string) {
    onSubmit(form.getFieldsValue(), skillsForm, type);
    form.submit();
  }

  useEffect(() => {
    getCommonOptions();

    return () => {
      setOptions({
        department: [],
        workingTime: [],
        yearOfExperience: [],
        location: []
      });
    };
  }, []);

  useEffect(() => {
    if (item) {
      // set start date into date picker antd
      const startDate = dayjs(item?.startDate, dateFormat);
      const endDate = dayjs(item?.endDate, dateFormat);
      const skills = item.skills?.map((el: any) => el.skillName);
      const itemForm = { ...item, startDate, endDate, skills: '' };
      setSkillsForm(skills as string[]);
      form.setFieldsValue(itemForm);
    }

    return () => {
      form.resetFields();
    };
  }, [item]);

  return (
    <Form layout="vertical" form={form}>
      <h3 className="mb-4 font-medium">Information</h3>
      <Row gutter={10}>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        >
          <Form.Item name="department" label={t('common.label.department')} rules={[{ required: true }]}>
            <Select style={{ width: '100%' }} placeholder={t('common.label.type')} options={options.department} />
          </Form.Item>
        </Col>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        >
          <Form.Item name="jobName" label={t('common.label.jobName')} rules={[{ required: true }]} className="my-0">
            <Input placeholder={t('common.label.jobName')} />
          </Form.Item>
        </Col>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        >
          <Form.Item name="workingTime" label={t('form.jobs.label.workingTime')} rules={[{ required: true }]}>
            <Select placeholder={t('common.label.type')} options={options.workingTime}></Select>
          </Form.Item>
        </Col>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        >
          <Form.Item name="city" label={t('form.jobs.label.cityName')}>
            <Select placeholder={t('form.jobs.label.cityName')} options={options.location}></Select>
          </Form.Item>
        </Col>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        ></Col>
      </Row>
      <Row gutter={10}>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        >
          <Form.Item name="experience" label={t('form.jobs.label.experience')} rules={[{ required: true }]}>
            <Select placeholder={t('common.label.type')} options={options.yearOfExperience}></Select>
          </Form.Item>
        </Col>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        >
          <Form.Item name="startDate" label={t('form.jobs.label.startDate')} rules={[{ required: true }]}>
            <DatePicker
              placeholder={t('form.jobs.label.startDate_placeholder')}
              format={dateFormat}
              maxDate={dayjs(form.getFieldValue('endDate'))}
            />
          </Form.Item>
        </Col>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        >
          <Form.Item name="endDate" label={t('form.jobs.label.endDate')} rules={[{ required: true }]}>
            <DatePicker
              placeholder={t('form.jobs.label.endDate_placeholder')}
              format={dateFormat}
              minDate={dayjs(form.getFieldValue('startDate'))}
            />
          </Form.Item>
        </Col>
        <Col
          xl={{
            span: 6
          }}
          md={{
            span: 12
          }}
        >
          <Form.Item
            name="budget"
            label={t('form.jobs.label.budget')}
            rules={[
              {
                required: true
              }
            ]}
          >
            <Input placeholder={t('form.jobs.label.budget')} />
          </Form.Item>
        </Col>
      </Row>
      <Row gutter={10}>
        <Col
          xl={{
            span: 12
          }}
          md={{
            span: 24
          }}
        >
          <Form.Item
            name="skills"
            label={t('common.label.skills')}
            rules={[{ required: skillsForm.length === 0 }]}
            className="my-0"
          >
            <Input placeholder={t('common.label.skills')} onKeyDown={onSkillsChange} />
          </Form.Item>
          <div className="skills my-3">
            {skillsForm &&
              skillsForm?.map((el: any, id) => {
                return (
                  <Tag key={id} className="custom-tag my-1">
                    <span>
                      {el}
                      <span
                        className="close-icon ml-2"
                        onClick={() => {
                          onRemoveSkill(el);
                        }}
                      >
                        <CloseOutlined width={8} height={8} className="cursor-pointer" />
                      </span>
                    </span>
                  </Tag>
                );
              })}
          </div>
        </Col>
      </Row>
      <Form.Item name="description" label={t('form.jobs.label.description')} rules={[{ required: true }]}>
        <Input.TextArea placeholder={t('form.jobs.label.description')} showCount rows={4} maxLength={100} />
      </Form.Item>
      <Form.Item name="requirement" label={t('form.jobs.label.requirement')} rules={[{ required: true }]}>
        <Input.TextArea placeholder={t('form.jobs.label.requirement')} showCount rows={4} maxLength={100} />
      </Form.Item>
      <Form.Item name="preferredQualifications" label={t('form.jobs.label.preferredQualifications')}>
        <Input.TextArea placeholder={t('form.jobs.label.preferredQualifications')} showCount rows={4} maxLength={100} />
      </Form.Item>
      <Form.Item>
        <div className="w-full flex justify-end items-center gap-2 mt-6">
          <Button onClick={() => navi(-1)} className="h-10 w-52">
            {t('common.label.cancel')}
          </Button>

          {item && (
            <button
              className="bg-blue-500 hover:bg-blue-600 text-white h-10 w-52"
              onClick={() => handleSubmitForm('duplicate')}
              disabled={isProcess}
            >
              {t('common.label.duplicate')}
            </button>
          )}
          <Button className="h-10 w-52" type="primary" danger onClick={() => handleSubmitForm()} disabled={isProcess}>
            {textButton || t('common.label.create')}
          </Button>
        </div>
      </Form.Item>
    </Form>
  );
}

export default JobForm;
